<template>
  <v-container>
    <v-list>
      <v-list-item-group>
        <v-list-item v-for="(post, index) in reversedPosts" :key="post._id" class="custom-list-item" @click="toggleContents(post)">
          <v-list-item-content>
            <v-list-item-title>{{ post.title }}</v-list-item-title>
            <v-list-item-subtitle>
              작성자: {{ post.userId }}   작성시간: {{ formatDate(post.register_date) }}
            </v-list-item-subtitle>
           
            <v-list-item-content v-if="selectedPost === post">
              <span style="font-size: 13px; font-weight: bold;  color:rgb(67, 67, 67)">
                본문
              </span>
              <hr>
              <span v-html="formatLineBreaks(post.contents)" style="font-size: 13px; font-style: italic; color:rgb(67, 67, 67)"></span>
            </v-list-item-content>
          </v-list-item-content>
          <v-btn v-if="post.userId===userId" @click="deleteBoard(post.userId,post.title,post.contents)">삭제</v-btn>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </v-container>
</template>

<script>
export default {
  props: {
    posts: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      selectedPost: null,
      userId:'Im_IronMan'
    };
  },
  computed: {//computed는 Vue.js에서 제공하는 속성으로, 데이터의 변화에 따라 계산된 값을 반환하는 데 사용한다고 핟나.
    reversedPosts() {
      // 배열을 뒤집어서 반환
      return this.posts.slice().reverse();
    },
  },
  methods: {
    formatDate(timestamp) {
      const date = new Date(timestamp);
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      return `${year}-${month}-${day} ${hours}:${minutes}`;
    },
    toggleContents(clickedPost) {
      // 클릭한 리스트의 post.contents를 토글하고 selectedPost 업데이트
      this.selectedPost = this.selectedPost === clickedPost ? null : clickedPost;
    },
    formatLineBreaks(text) { //v-html="formatLineBreaks(post.contents) 식으로 <br>문자가 생기면 html요소로 인식
      // 줄바꿈 문자를 <br> 태그로 바꾸기
      return text.replace(/\n/g, '<br>');
    },
    deleteBoard(Id,title,contents){ //데이터 삭제
      this.axios.delete('http://localhost:8000/boards/'+Id+'/'+title+'/'+contents,{ 
              userId: this.userId, title:this.title, contents:this.contents
         },{withCredentials:true})
           .then((res) => {
                 alert('게시글 삭제 성공!');
                
           })
             .catch((err) => {
                alert('에러 발생: ' + err); //에러 발생
          });          
    }
  },
};
</script>

<style scoped>

.custom-list-item {
  border-bottom: 1px solid rgb(189, 159, 155); /* 각 v-list-item 간의 경계선을 설정 */
}

</style>