<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      width="800"
    >
      <template v-slot:activator="{ props }">
        <v-btn
          color="primary"
          v-bind="props"
          @click="dialog=true"
        >
          재료구매하기
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="text-h5">재료 구매하기</span>
        </v-card-title>
        <table border="1">
        <tr v-for = "product in products" :key="product.name"  >
          <td>
            <img :src="product.image">
          </td>
          <td>
            <span style="font-weight: bold;">
              {{ product.title }}
            </span><br>
            <span style="color: red;">
              {{ product.lprice }} 원
            </span>
          </td>
          <td>
            <v-btn @click="openLink(product.link)">구매</v-btn>
          </td>
        </tr>
      </table>
        <v-card-actions>
          <v-overlay :value="loading">
            <v-progress-circular
              indeterminate
              color="my_color1"
              size="70"
            > 
            <span style="font-size: 20px;">로딩중</span>
            </v-progress-circular>
          </v-overlay>
          <v-spacer></v-spacer>
          <v-btn
            color="green-darken-1"
            variant="text"
            @click="dialog = false"
          >
            닫기
          </v-btn>
         
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>



  

<script setup>
  import { ref } from 'vue'

  const dialog = ref(false)
</script>


<script>

  export default {
    name: 'Shop',
    created() {
      //이벤트 구독
      this.$bus.$on('shop', (ingredientList) => {
        this.search(ingredientList);
      });
    },
    methods: {
      search(ingredients) {
          this.products = []; // 시작 전에 항상 초기화
          // 로딩 상태를 시작
          
          for (const ingredient of ingredients) {
            const apiUrl = `/v1/search/shop.json?query=${encodeURIComponent(ingredient)}&display=3`;
            this.loading = true;
            this.axios.get(apiUrl, {
              headers: {
                'X-Naver-Client-Id': 'uc23LP0UWNTQ7hJ3rHlV',
                'X-Naver-Client-Secret': 'Ak7kBYp_pF',
              },
              withCredentials: true
            })
              .then(response => {
                const data = response.data.items;
                if (data && data.length > 0) {
                  for (const item of data) {
                    const object = {
                      title: item.title.replace(/<[^>]*>/g, ''), // HTML 태그 제거, 정규 표현식을 사용하여 <b>같은 문자열안의 태그 삭제
                      link: item.link,
                      lprice: item.lprice,
                      image: item.image,
                    };
                    this.products.push(object);
                  }
                } else {
                  console.error('No items found.');
                }
              })
              .catch(error => {
                console.error(error);
              })
              .finally(() => {
                // 로딩 상태를 종료
                this.loading = false;
              });    
          }
        }
        ,
        openLink(link) { // 새로운 창을 연다.
          window.open(link, '_blank'); //_blank는 새로운 창을 열게하는 매개변수이다.
        },
    },
    data() {
      return {
        products:[], // 상품들을 담을 배열
        dialog: false,
         loading: false, // 로딩 상태를 나타내는 변수
      };
    },
  };
</script>

<style scoped>
  .contents {
  align-items: center;
  text-align: center;
  margin-top: 10px;
}
  img {
    
    max-width: 40px; /* 최대 크기 설정 */
    height: auto; /* 가로 비율에 따라 세로 크기 자동 조정 */
  }
</style>